export class MeasureValues {
  constructor(
      public runIndex: number, public timeStamp: Date, public values: {[key: string]: any}) {}

  toJson() {
    return {
      'timeStamp': this.timeStamp.toJSON(),
      'runIndex': this.runIndex,
      'values': this.values,
    };
  }
}
